body {
  text-align: center;
  font-size: 1em;
  font-family: verdana;
  margin: 0;
}
h1 {
  font-size: 1.2em;
  padding: 20px 0;
  border-bottom: 1px solid #eee;
  margin: 0 0 30px 0;
  color: #333;
}

.row {
  text-align: center;
  display: flex;
  justify-content: center;
}

.row > div {
  display: block;
  width: 60px;
  height: 60px;
  border: 1px solid #bbb;
  margin: 4px;
  text-align: center;
  line-height: 60px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.5em;
  border-radius: 5px;
}

.row > div.green {
  --background: #6ec531;
  --border-color: #6ec531;
  animation: flip 0.5s ease forwards;
}
.row > div.grey {
  --background: #a1aeb1;
  --border-color: #a1aeb1;
  animation: flip 0.5s ease forwards;
}
.row > div.yellow {
  --background: #FFE417;
  --border-color: #FFE417;
  animation: flip 0.5s ease forwards;
}
.row > div:nth-child(2){
  animation-delay: 0.2s;
}
.row>div:nth-child(3) {
  animation-delay: 0.4s;
}
.row>div:nth-child(4) {
  animation-delay: 0.6s;
}
.row>div:nth-child(5) {
  animation-delay: 0.8s;
}

.row.current > div.filled {
  animation: bounce 0.4s ease-in-out forwards;
}

.keypad {
  max-width: 500px;
  margin: 0 auto;
}

.keypad > div {
  margin: 5px;
  width: 40px;
  height: 50px;
  background: #eee;
  display: inline-block;
  border-radius: 6px;
  line-height: 50px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
    transition: all 0.1s ease-in-out;
}

.keypad>div:active {
  background: #ddd;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
  transform: translateY(1px);
}

.keypad > div.green {
  background: #6ec531;
  color: #fff;
  border-color: #6ec531;
  transition: all 0.3s ease-in;
}

.keypad > div.yellow {
  background: #FFE417;
  color: #fff;
  transition: all 0.3s ease-in;
}

.keypad > div.grey {
  background: #a1aeb1;
  color: #fff;
  transition: all 0.3s ease-in;
}

.keypad>div.enter-button {
  width: 100px;
}


.modal {
  background: rgba(255, 255, 255, 0.7);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.modal div {
  max-width: 480px;
  background: #fff;
  padding: 40px;
  border-radius: 10px;
  margin: 10% auto;
  box-shadow: 2px 2px 10px rgba(0,0,0,0.3);
}

.modal .solution {
  color: purple;
  font-weight: bolder;
  font-size: 1.8em;
  text-transform: uppercase;
  letter-spacing: 1px;
}

@keyframes flip {
  0% {
    transform: rotateY(0deg);
    background: #fff;
  }
  45% {
    transform: rotateY(90deg);
        background: #fff;
          border-color: #a1aeb1;
  }
  55% {
    transform: rotateY(90deg);
    background: var(--background);
    border-color: var(--border-color);
    color: #fff;
  }
  100% {
    transform: rotateY(0deg);
    background: var(--background);
    border-color: var(--border-color);
    color: #fff;
  }
}

@keyframes bounce {
  0% {
    transform: scale(1);
    border-color: #ddd;
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
    border-color: #333;
  }
}
